"use client";

import { usePathname } from "next/navigation";
import Link from "next/link";
import { Button } from "@/components/ui/button";
import {
  Lightbulb,
  Brain,
  Layers,
  Lock,
  Eye,
  Calendar,
  Trash2,
} from "lucide-react";
import posthog from 'posthog-js';

const navItems = [
  {
    href: "/app",
    label: "Notes",
    icon: Lightbulb,
    color: "text-[var(--color-brand-lime)]",
  },
  {
    href: "/app/mindstorm",
    label: "MindStorm",
    icon: Brain,
    color: "text-[var(--color-brand-indigo)]",
  },
  {
    href: "/app/stacks",
    label: "Smart Stacks",
    icon: Layers,
    color: "text-[var(--color-brand-lime)]",
  },
  {
    href: "/app/vault",
    label: "Vault",
    icon: Lock,
    color: "text-[var(--color-brand-coral)]",
  },
  {
    href: "/app/insights",
    label: "Insights",
    icon: Eye,
    color: "text-[var(--color-brand-indigo)]",
  },
  {
    href: "/app/memory",
    label: "Memory Lane",
    icon: Calendar,
    color: "text-[var(--color-brand-lime)]",
  },
  {
    href: "/app/nope",
    label: "Nope Bin",
    icon: Trash2,
    color: "text-muted-foreground",
  },
];

interface SidebarNavProps {
  activeRoute?: string;
}

export function SidebarNav({ activeRoute }: SidebarNavProps) {
  const pathname = usePathname();

  return (
    <nav className="flex flex-col gap-1 p-4">
      {navItems.map((item) => {
        const Icon = item.icon;
        const isActive = activeRoute
          ? pathname === activeRoute
          : pathname === item.href;

        return (
          <Button
            key={item.href}
            variant="ghost"
            className={`justify-start gap-3 ${isActive ? "bg-accent" : ""}`}
            asChild
          >
            <Link href={item.href} onClick={() => {
              posthog.capture('sidebar_navigation_link_clicked', {
                target_href: item.href,
                target_label: item.label,
              });
            }}>
              <Icon
                className={`h-4 w-4 ${item.color || "text-muted-foreground"}`}
              />
              {item.label}
            </Link>
          </Button>
        );
      })}
    </nav>
  );
}
